import React, { useEffect, useRef, useState } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

function Works() {
    gsap.registerPlugin(ScrollTrigger);

    const dataWorks = [
        {
            id: 2,
            title: "GREEN",
            title2: "PLUGGED",
            side: "Web Design",
        },
        {
            id: 3,
            title: "CLASSIC",
            title2: "BLACK & BROWN",
            side: "PhotoGraphy",
        },
        {
            id: 4,
            title: "MOODY",
            title2: "CONTRAST",
            side: "Motion Graphic",
        },
    ];
    const titleRef = useRef();
    const revealRef = useRef([]);
    revealRef.current = [];

    useEffect(() => {
        revealRef.current.forEach((e, index) => {
            gsap.fromTo(
                e,
                {
                    y: 0,
                    opacity: 0,
                },
                {
                    y: 100,
                    duration: 1.5,
                    opacity: 1,

                    scrollTrigger: {
                        trigger: e,

                        start: "-300% top",
                        toggleActions: "play none none none",
                    },
                }
            );
        });

        gsap.fromTo(
            titleRef.current,
            {
                y: 0,
                opacity: 0,
            },
            {
                y: 100,
                duration: 1.5,
                opacity: 1,

                scrollTrigger: {
                    trigger: titleRef.current,

                    start: "50% center",
                    toggleActions: "play none none none",
                },
            }
        );
    }, []);

    const addToRef = (e) => {
        if (e && !revealRef.current.includes(e)) {
            revealRef.current.push(e);
        }
    };

    return (
        <div className="works">
            <div className="col_1" ref={titleRef}>
                <div className="img">
                    <img src={"/Assets/img/col_1.jpg"} />
                </div>
                <ul>
                    <li>Feature Work</li>
                    <li>Our Project</li>
                    <li>
                        &nbsp;&nbsp;저희는 이미지, 동영상, 3D등을 직접
                        모델링하여 더 높은 품질의 디자인을 생산합니다. 우수한
                        자원을 바탕으로 우리는 고객의 브랜드를 더욱 아름답고
                        효율적으로 홍보합니다. <br></br> <br></br>
                        &nbsp;&nbsp;저희는 고객만족을 위하여 '보다 높은 품질,
                        보다 앞선 디자인, 보다 전문적인 서비스'를 추구하고 모든
                        분야에서 전문적인 인력들이 최선을 다하고 있습니다.
                        <br></br>
                        <br></br>&nbsp;&nbsp;아래는 우리가 만든 디자인
                        샘플입니다.저희와 함께 고객님의 브랜드의 웹사이트를 더
                        활기차고 아름답게 만들어보세요.
                    </li>
                </ul>
            </div>

            {dataWorks.map((e, i) => {
                return (
                    <div
                        className={"col col_" + (i + 2)}
                        key={i}
                        ref={addToRef}
                    >
                        <div className="wrapper">
                            <div className="img">
                                <img
                                    src={
                                        "/Assets/img/col_" +
                                        dataWorks[i].id +
                                        ".jpg"
                                    }
                                />
                                <p className="right_wing">
                                    {dataWorks[i].side}
                                </p>
                            </div>
                            <div className="bot_title">
                                <p>{dataWorks[i].title}</p>
                                <p>{dataWorks[i].title2}</p>
                            </div>
                        </div>
                    </div>
                );
            })}
        </div>
    );
}

export default Works;
