import React from "react";
import { useEffect, useState, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "./about.scss";

import frontImg1 from "../../Assets/img/frontImg1.jpg";
import frontImg2 from "../../Assets/img/frontImg2.png";

let About = () => {
    const boxRef = useRef();
    const textRef = useRef();
    const textRef2 = useRef();
    gsap.registerPlugin(ScrollTrigger);

    useEffect(() => {
        const cl = document.querySelector(".App");
        cl.style.backgroundColor = "black";
        console.log("안녕");

        gsap.fromTo(
            boxRef.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 1,
                y: 0,
                scrollTrigger: {
                    trigger: boxRef.current,
                    start: "top center+=100",

                    toggleActions: "play none none none",
                },
            }
        );

        gsap.fromTo(
            textRef.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 2,
                y: 0,
                scrollTrigger: {
                    trigger: textRef.current,
                    start: "top center-=100",

                    toggleActions: "play none none none",
                },
            }
        );

        gsap.fromTo(
            textRef2.current,
            {
                opacity: 0,
                y: -100,
            },
            {
                opacity: 1,
                duration: 1.5,
                y: 0,
                scrollTrigger: {
                    trigger: textRef2.current,
                    start: "top += 1000",
                    toggleActions: "play none none none",
                },
            }
        );
    }, []);

    return (
        <>
            <section className="bgColorAbout">
                <div className="circleTransition"></div>
                <div className="aboutPage">
                    <div className="largeTxt">
                        <p>THIS</p>
                        <p>STUDIO</p>
                    </div>
                    <div className="smallTxt">
                        <div className="text_thing" ref={textRef}>
                            <h1>Hello World!</h1>
                            <p>
                                &nbsp;&nbsp;우리는 고객의 목소리에 귀기울이고
                                그에 걸맞는 맞춤형 서비스를 제공하여, 고객을
                                위한 탁월한 브랜드 및 웹 경험을 제공할 수 있다고
                                믿습니다. 우리는 열정있는 브랜드와 협력하여
                                즐겁고 아름다운 결과물을 만드는 것을 좋아합니다.
                            </p>
                        </div>

                        <div className="threedCube">
                            <img src={frontImg1} ref={boxRef} />
                        </div>
                    </div>
                    <div className="smallTxt">
                        <div className="threedCube">
                            <img src={frontImg2} />
                        </div>
                        <div className="text_thing" ref={textRef2}>
                            <h1>We are Jude!</h1>
                            <p>
                                &nbsp;&nbsp;우리는 3D 모델링과 웹디자인 그리고
                                비디오와 사진과 같은 소스부터 최종 결과물인
                                웹서비스까지 모든 것을 제공합니다. 이 모든 전체
                                프로세스는 전문적인 인력과 함께 유기적이고
                                효율적으로 구현되며, 동시에 세밀한 부분들도
                                놓치지 않도록 모든 분야에서 최선의 노력을
                                기울이고 있습니다.
                            </p>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
};

export default About;
